import { Trans } from '@lingui/macro'
import { MINUTE, useNow } from '@speedlo/hooks'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { DeliveryMinutes } from '../../../atoms/DeliveryMinutes'
import { FormInputStyled } from '../../../atoms/FormInputStyled'
import { Sizer } from '../../../atoms/Sizer'
import {
  DeliveryTypeEnum,
  useMFastClose,
  useQIsBranchOpen,
} from '../../../graph/generated'
import { useSmartDialog } from '../../../hooks/useSmartDialog'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { DF, DFLocale } from '../../../tools/datefns'
import { useOrderListModel } from '../hooks/useOrderListModel'
import { FastCloseButton } from '../molecules/FastCloseButton'

export const FastClose = observer(() => {
  const getNow = useNow()
  const orderListModel = useOrderListModel()
  const branchId = orderListModel.firstBranchId

  const { getStash, startPolling, stopPolling } = useQIsBranchOpen(
    { branchId: branchId! },
    { skip: branchId === null },
  )

  const [doFastClose] = useMFastClose()

  const { enqueueSuccess } = useSnackBar()

  const inputRef = React.useRef<HTMLInputElement>(null)

  const onConfirm = async (minutes, onSuccess) => {
    const note = inputRef.current!.value.trim()
    const result = await doFastClose({
      branchId: branchId!,
      interval: minutes,
      note,
    })
    if (result) {
      const now = getNow()
      const minutesLocalized = DF.formatDistanceStrict(
        DF.addMinutes(now, minutes),
        now,
        { locale: DFLocale, addSuffix: true, unit: 'minute' },
      )
      onSuccess()
      enqueueSuccess(i18n.t`Branch will open again ${minutesLocalized}.`)
    }
  }

  const isOpen = getStash(data => data.companyBranch.isOpen)

  React.useEffect(() => {
    if (isOpen === true) {
      stopPolling()
    } else {
      startPolling(MINUTE)
    }
  }, [isOpen, startPolling, stopPolling])

  const [dialog, { open }] = useSmartDialog({
    title: i18n.t`Fast close branch (for minutes)`,
    content: ({ close }) => (
      <>
        <Sizer width={20}>
          <FormInputStyled
            placeholder={i18n.t`Close reason`}
            autoComplete="off"
            inputFullWidth={true}
            ref={inputRef}
            onFocus={() => inputRef.current!.select()}
            defaultValue={i18n.t`We are closed at the moment`}
          />
        </Sizer>
        <DeliveryMinutes
          deliveryType={DeliveryTypeEnum.Pickup}
          onMinuteClick={async minutes => {
            onConfirm(minutes, close)
          }}
        />
      </>
    ),
    actions: ({ close }) => (
      <TouchButton onClick={close}>
        <Trans>Cancel</Trans>
      </TouchButton>
    ),
  })

  return (
    <>
      <FastCloseButton
        branchId={branchId}
        isBranchClosed={isOpen === false}
        onClick={open}
      />
      {dialog}
    </>
  )
})
