import { Tooltip } from '@material-ui/core'
import { Attachment } from '@material-ui/icons'
import { SECOND } from '@speedlo/hooks'
import React from 'react'
import styled from 'styled-components/macro'

import { mediaMobile } from '../../../app/Responsive'
import { Card } from '../../../atoms/Card'
import { ColorBadge } from './ColorBadge'
import { formatPrice } from '../../../helpers/formats'
import { useLongPress } from '../../../hooks/useLongPress'
import { applyAlphaToColor, Colors, Radiuses } from '../../../styling'
import { TOrderMenuRecipe } from '../createorder.types'

interface IProps {
  recipe: TOrderMenuRecipe
  onUse(withSidedish: boolean): void
}

const applyAlpha = (alphaValue: number) => () =>
  applyAlphaToColor(Colors.yellow, alphaValue)

const StyledOrderMenuRecipeCard = styled(Card)`
  box-sizing: border-box;
  overflow: hidden;
  height: 5.8rem;
  cursor: pointer;
  margin: 0 0.5rem 0.5rem 0;
  padding: 0.2rem 0.1rem 0.1rem 0.5rem;
  &:hover {
    background-color: ${applyAlpha(0.85)};
  }
  user-select: none !important;

  .recipe-name {
    padding-top: 0.2rem;
    ${mediaMobile`
    font-size: 0.7rem;
  `};
  }

  .recipe-code {
    font-weight: bold;
  }

  .recipe-price {
    position: absolute;
    right: 0.6rem;
    bottom: 0.2rem;
    font-weight: 500;
    font-size: 1.1rem;
    color: ${Colors.greyDarkest};
    text-align: right;
    overflow: hidden;
    white-space: nowrap;

    ${mediaMobile`
    font-size: 0.7rem;
    padding: 0.2rem;
    background: ${Colors.greyLight};
    right: 0;
    bottom: 0;
    border-bottom-right-radius: ${Radiuses.Card};
  `};
  }
`

const RecipeHasSidedish = styled(Attachment)`
  position: absolute;
  right: 0.6rem;
  top: 0.2rem;
` as typeof Attachment

export const OrderMenuRecipeCard: React.FC<IProps> = ({ recipe, onUse }) => {
  const longPress = useLongPress(() => {
    onUse(true)
  })
  return (
    <Tooltip title={recipe.descriptionLabel || ''} enterDelay={SECOND}>
      <StyledOrderMenuRecipeCard
        onClick={ev => {
          if (!ev.defaultPrevented) {
            onUse(false)
          }
        }}
        onContextMenu={ev => {
          ev.preventDefault()
        }}
        {...longPress}
      >
        <ColorBadge color={recipe.categoryColor} />
        <div className="recipe-code">{recipe.code}</div>
        <div className="recipe-name">{recipe.nameLabel}</div>
        <div className="recipe-price">
          {formatPrice(recipe.price.value, recipe.price.currency.code)}
        </div>
        {recipe.sidedishCount > 0 ? (
          <RecipeHasSidedish
            onClickCapture={ev => {
              ev.preventDefault()
              onUse(true)
            }}
          />
        ) : null}
      </StyledOrderMenuRecipeCard>
    </Tooltip>
  )
}
