import { disableBatching } from '@speedlo/graphql'
import { Sentry } from '@speedlo/sentry'

import {
  QOrdersCategoryCountOrdersCategoryCount,
  useQOrdersCategoryCount,
  useSOrdersCategoryCount,
} from '../../../graph/generated'
import { useOnConnectionRestored } from '../../../hooks/useOnConnectionRestored'
import { RM } from '../../../tools/ramda'
import { TOrderCategory } from '../order.types'
import { useOrderListQueryControl } from './useOrderListQueryControl'

export function useOrderListCounts(allCategories: RoA<TOrderCategory>) {
  const { branchesFilter, skipQuery } = useOrderListQueryControl(allCategories)

  const { data, loading, refetch, updateQuery } = useQOrdersCategoryCount(
    { branchesId: branchesFilter, categories: allCategories },
    {
      fetchPolicy: 'cache-and-network',
      skip: skipQuery,
      ...disableBatching,
    },
  )

  useOnConnectionRestored(refetch)

  useSOrdersCategoryCount(
    { branchesId: branchesFilter, categories: allCategories },
    {
      skip: skipQuery,
      fetchPolicy: 'network-only',
      onSubscriptionData({ subscriptionData: update }) {
        if (!(update && update.data && update.data.updatedCount)) {
          return
        }

        const { nextCategory, prevCategory } = update.data.updatedCount
        const getUpdatedCount = ({
          category,
          count,
        }: QOrdersCategoryCountOrdersCategoryCount) => {
          if (category.enum === prevCategory) {
            return count - 1
          }
          if (category.enum === nextCategory) {
            return count + 1
          }
          return count
        }

        updateQuery(prev => {
          if (!(prev && prev.ordersCategoryCount)) {
            return prev
          }
          return {
            ...prev,
            ordersCategoryCount: prev.ordersCategoryCount.map(item => ({
              ...item,
              count: getUpdatedCount(item),
            })),
          }
        })
      },
    },
  )

  const getCategoryCount = (category: TOrderCategory) => {
    if (!allCategories.includes(category)) {
      return 'disabled'
    }
    if (loading) {
      return 'loading'
    }
    const found =
      data &&
      data.ordersCategoryCount.find(RM.pathEq(['category', 'enum'], category))
    if (!found) {
      Sentry.captureMessage(
        `Category ${category} wasn't returned from Query.ordersCategoryCount`,
        Sentry.Severity.Error,
      )
      return 'disabled'
    }
    return found.count
  }

  return { getCategoryCount, categoryCountIsReady: !loading }
}
