import { useFetch } from './useFetch'

export interface IAddressSearchOptions {
  count?: number
  zoom?: number
  center?: {
    latitude: number
    longitude: number
  }
}

export type AddressSearchResult = ReturnType<typeof mapResult>

export function useAddressSearch(
  phrase: string = '',
  options: IAddressSearchOptions = {},
) {
  const url = buildUrl(phrase, options)

  const { data, fetching } = useFetch({ url, lazy: phrase.length === 0 })

  return {
    loading: fetching,
    results: data
      ? (data.result.filter(validateResult).map(mapResult) as RoA<
          AddressSearchResult
        >)
      : null,
  }
}

function validateResult({ userData }) {
  const { latitude, longitude } = userData
  return latitude && longitude
}

function mapResult({ userData }) {
  const {
    latitude,
    longitude,
    suggestFirstRow,
    suggestSecondRow,
    municipality,
    district,
    country,
  } = userData
  const suffix =
    [municipality, district, country, suggestSecondRow].find(Boolean) || ''
  return {
    gps: {
      latitude,
      longitude,
    },
    address: `${suggestFirstRow}${suffix ? `, ${suffix}` : ''}`,
  }
}

function buildUrl(
  phrase: string,
  { count = 1, zoom, center }: IAddressSearchOptions,
) {
  const params = [
    `phrase=${phrase}`,
    `count=${count}`,
    `lang=cs,sk,en`,
    'type=street|address|poi',
    'enableCategories=1',
  ]
  if (center) {
    params.push(`lon=${center.longitude}`, `lat=${center.latitude}`)
  }
  if (zoom) {
    params.push(`zoom=${zoom}`)
  }

  return `https://api.mapy.cz/suggest/?${params.join('&')}`
}
