import { observer } from 'mobx-react-lite'
import React from 'react'

import { useGoogleMap } from '../../../atoms/BaseMap'
import {
  DeliveryTypeEnum,
  OrderStateCategoryEnum,
  QOrderMapOrdersQuery,
  SOrderMapOrdersSubscription,
  useQOrderMapOrders,
  useSOrderMapOrders,
} from '../../../graph/generated'
import { gpsToLatLng } from '../../../helpers/gps'
import { useOnConnectionRestored } from '../../../hooks/useOnConnectionRestored'
import { idPropEq } from '../../../tools/ramda'
import { OrderMapMarker } from '../atoms/OrderMapMarker'
import { useOrderListModel } from '../hooks/useOrderListModel'
import { useOrderListQueryControl } from '../hooks/useOrderListQueryControl'

export const orderCategoriesOnMap = [
  OrderStateCategoryEnum.NewOrders,
  OrderStateCategoryEnum.Preparing,
  OrderStateCategoryEnum.ReadyToDeliver,
  OrderStateCategoryEnum.OnWay,
]

export const OrderMapOrders = observer(() => {
  const orderListModel = useOrderListModel()

  const queryCategories = orderCategoriesOnMap.filter(
    category => !orderListModel.collapsedCategories.includes(category),
  )

  const { branchesFilter: branches, skipQuery } = useOrderListQueryControl(
    queryCategories,
  )

  const { getStash, refetch, updateQuery } = useQOrderMapOrders(
    { branches, categories: queryCategories },
    { skip: skipQuery },
  )

  useOnConnectionRestored(refetch)

  useSOrderMapOrders(
    { branches, categories: queryCategories },
    {
      skip: skipQuery,
      onSubscriptionData({ subscriptionData }) {
        if (subscriptionData.data) {
          updateQuery(prev =>
            onOrderMapOrdersUpdate(subscriptionData.data!, prev),
          )
        }
      },
    },
  )

  const orders = getStash(data => {
    return data.orders.filter(
      order =>
        orderCategoriesOnMap.includes(order.orderStateCategory) &&
        order.deliveryType &&
        order.deliveryType.enum === DeliveryTypeEnum.Messenger,
    )
  })

  const map = useGoogleMap()

  React.useEffect(() =>
    orderListModel.observerOrderDetail(change => {
      if (orders !== null && change.type === 'add') {
        const order = orders.find(idPropEq(change.newValue))
        if (order && order.gps) {
          map.panTo(gpsToLatLng(order.gps))
        }
      }
    }),
  )

  if (skipQuery || orders === null) {
    return null
  }

  const renderOrder = order => (
    <OrderMapMarker
      key={order.id}
      order={order}
      onClick={orderListModel.focusOrderDetail}
    />
  )

  return <>{orders.map(renderOrder)}</>
})

function onOrderMapOrdersUpdate(
  update: SOrderMapOrdersSubscription,
  prev: QOrderMapOrdersQuery,
): QOrderMapOrdersQuery {
  const prevOrders = prev ? [...prev.orders] : []
  const { order } = update.updatedOrder
  if (!order) {
    return { orders: prevOrders }
  }
  let orderExists = false
  const orders = prev.orders.map(prevOrder => {
    if (prevOrder.id === order.id) {
      orderExists = true
      return order
    }
    return prevOrder
  })
  if (!orderExists) {
    orders.unshift(order)
  }
  return { ...prev, orders }
}
