import React from 'react'
import styled from 'styled-components'

import { FOrderMenuCategoryFragment } from '../../../graph/generated'
import { OrderMenuCategory } from '../atoms/OrderMenuCategory'

interface IProps {
  categories: RoA<FOrderMenuCategoryFragment>
  activeCategory: MaybeID
  onCategoryClick(categoryId: ID): void
}

const FlexFlow = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
`

export const OrderMenuCategories: React.FC<IProps> = ({
  categories,
  activeCategory,
  onCategoryClick,
}) => {
  return (
    <FlexFlow>
      {categories
        .filter(cat => cat.recipes.length > 0)
        .map(category => (
          <OrderMenuCategory
            key={category.id}
            category={category}
            isActive={category.id === activeCategory}
            onClick={() => onCategoryClick(category.id)}
          />
        ))}
    </FlexFlow>
  )
}
