import { Store } from '@material-ui/icons'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { useRoot } from '../../../atoms/RootProvider'
import { useAutorunWhenSingleBranch } from '../../../hooks/useAutorunWhenSingleBranch'
import { i18n } from '../../../i18n'
import { CompanyBranchPicker } from '../../../organisms/CompanyBranchPicker'

export const OrderFormBranch = observer(() => {
  const { user, createOrder } = useRoot()

  useAutorunWhenSingleBranch(branch => {
    createOrder.selectBranch(branch.id)
  })

  React.useEffect(() => {
    if (!createOrder.branchId) {
      createOrder.selectBranch(user.defaultBranch.id)
    }
  }, [createOrder, user.defaultBranch.id])

  const onSelectionChange = React.useCallback(
    ([branchId]: RoA<ID>) => {
      if (
        !createOrder.orderId ||
        window.confirm(
          i18n.t`Changing branch will cancel current order and remove all items from the cart. Proceed?`,
        )
      ) {
        createOrder.selectBranch(branchId)
        createOrder.resetOrder()
      }
    },
    [createOrder],
  )

  if (user.hasSingleBranch) {
    return null
  }

  return (
    <IconLine icon={Store}>
      <CompanyBranchPicker
        isClearable={false}
        selectedBranches={[createOrder.currentBranch.id]}
        hideWhenSingleBranch={true}
        onSelectionChange={onSelectionChange}
      />
    </IconLine>
  )
})
