import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { CardListTitle } from '../../../atoms/CardListTitle'
import { OrderStateCategoryEnum } from '../../../graph/generated'
import { useOrderListCategories } from '../hooks/useOrderListCategories'
import { useOrderListCounts } from '../hooks/useOrderListCounts'
import { useOrderListOrders } from '../hooks/useOrderListOrders'
import { useOrderListQueryControl } from '../hooks/useOrderListQueryControl'
import { OrderListCategory } from '../molecules/OrderListCategory'
import { OrderListNotification } from '../molecules/OrderListNotification'

export const OrderList = observer(() => {
  const {
    allCategories,
    queryCategories,
    getCategoryLabel,
  } = useOrderListCategories()

  const { getCategoryCount } = useOrderListCounts(allCategories)

  const { getCategoryOrders, ordersLoading } = useOrderListOrders(
    queryCategories,
  )

  const { branchesFilter, noFilter } = useOrderListQueryControl(queryCategories)

  if (noFilter) {
    return (
      <CardListTitle>
        <Trans>Select at least one branch</Trans>
      </CardListTitle>
    )
  }

  const notificationOrders = getCategoryOrders(OrderStateCategoryEnum.NewOrders)

  return (
    <>
      {allCategories.map(category => (
        <OrderListCategory
          key={category}
          category={category}
          categoryLabel={getCategoryLabel(category)}
          orders={getCategoryOrders(category)}
          ordersCount={getCategoryCount(category)}
          ordersLoading={ordersLoading}
          showBranches={branchesFilter.length > 1}
        />
      ))}
      {notificationOrders.length > 0 && (
        <OrderListNotification orders={notificationOrders} />
      )}
    </>
  )
})
