import { Trans } from '@lingui/macro'
import { useDebouncedCallback } from '@speedlo/hooks'
import React from 'react'

import { useDownshift } from '../../atoms/DownshiftProvided'
import { useAddressSearch } from '../../hooks/useAddressSearch'
import { AddressSuggestItems } from './AddressSuggestItems'

interface IProps {
  searchCenter?: GeoPoint
  minLength?: number
  debounceInterval?: number
  initialIndex?: number
}

export const DiscoverSuggestions: React.FC<IProps> = ({
  searchCenter,
  minLength = 3,
  debounceInterval = 300,
  initialIndex,
}) => {
  const { inputValue } = useDownshift()

  const [searchValue, setSearchValue] = React.useState(inputValue || '')
  const [debouncedSearch] = useDebouncedCallback(
    setSearchValue,
    debounceInterval,
  )

  const { results, loading } = useAddressSearch(searchValue, {
    count: 5,
    center: searchCenter,
    zoom: searchCenter ? 12 : undefined,
  })

  React.useEffect(() => {
    if (inputValue && inputValue.length >= minLength) {
      debouncedSearch(inputValue)
    }
  }, [debouncedSearch, inputValue, minLength])

  if (!inputValue || inputValue.length === 0) {
    return (
      <div className="hint">
        <Trans>Start typing to search for an address</Trans>
      </div>
    )
  }

  if (loading || results === null) {
    return (
      <div className="hint">
        <Trans>Searching...</Trans>
      </div>
    )
  }

  return <AddressSuggestItems items={results} initialIndex={initialIndex} />
}
