import { useDebounce } from '@bit/speedlo.hooks.use-debounce'
import { CircularProgress } from '@material-ui/core'
import { Check, Close, WarningOutlined } from '@material-ui/icons'
import faker from 'faker'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { useRoot } from '../../../atoms/RootProvider'
import { useQOrderVamCustomer } from '../../../graph/generated'
import { Colors } from '../../../styling'

const ErrorIcon = styled(WarningOutlined)`
  font-size: 1rem;
  color: ${Colors.orange};
` as typeof WarningOutlined

const NotFoundIcon = styled(Close)`
  font-size: 1rem;
  color: ${Colors.greyMiddle};
` as typeof Close

const FoundIcon = styled(Check)`
  font-size: 1rem;
  color: ${Colors.green};
` as typeof Check

export const OrderFormCustomerLookup = observer(() => {
  const { createOrder } = useRoot()

  const [phoneNumber, cancelDebounce] = useDebounce(
    `${createOrder.phonePrefix || ''}${createOrder.phone || ''}`,
    300,
  )

  React.useEffect(() => cancelDebounce, [cancelDebounce])

  const { data, loading, error } = useQOrderVamCustomer(
    {
      branchId: createOrder.currentBranch.id,
      phone: phoneNumber,
    },
    { skip: !createOrder.isPhoneInputValid, fetchPolicy: 'no-cache' },
  )

  React.useEffect(() => {
    if (data) {
      if (data.vamCustomer && data.vamCustomer.isFound) {
        createOrder.updateFromVam(data.vamCustomer)
      } else {
        createOrder.updateFromVam(null)
      }
    }
  }, [createOrder, data])

  if (!createOrder.isPhoneInputValid) {
    return null
  }

  if (loading) {
    return <CircularProgress size={20} />
  }

  if (error) {
    return <ErrorIcon />
  }

  return data && data.vamCustomer.isFound ? <FoundIcon /> : <NotFoundIcon />
})

// @ts-ignore Can be useful when there is an error in DEV
// eslint-disable-next-line @typescript-eslint/no-unused-vars
function getFakeVam(): GOrderVam.VamInformation {
  return {
    firstName: 'FAKE',
    lastName: faker.name.lastName(),
    email: faker.internet.email(),
    lastOrder: {
      note: faker.lorem.sentence().slice(0, 50 - 1),
    },
    isFound: true,
    locations: [],
  }
}
