import React from 'react'

import { Desktop } from '../../../app/Responsive'
import { ETimeBadgeVariant, TimeBadge } from '../../../atoms/TimeBadge'
import {
  DeliveryTypeEnum,
  useMBranchDefaultTime,
} from '../../../graph/generated'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { useOrderListModel } from '../hooks/useOrderListModel'
import { useTimeSelectionDialog } from '../hooks/useTimeSelectionDialog'

type TProps = NoChildren & {
  deliveryType: DeliveryTypeEnum
  value: number
  title: string
  icon: ReactNode
  successText: string
}

export const BranchDefaultTimeBadge: React.FC<TProps> = ({
  deliveryType,
  value,
  title,
  icon,
  successText,
}) => {
  const [updateDeliveryTime] = useMBranchDefaultTime()
  const { enqueueSuccess } = useSnackBar()

  const [dialog, { open }] = useTimeSelectionDialog({
    deliveryType,
    title,
    async onConfirm(minutes, onSuccess) {
      const result = await updateDeliveryTime({
        branchId,
        deliveryType,
        time: minutes,
      })
      if (result) {
        onSuccess()
        enqueueSuccess(successText)
      }
    },
  })

  const orderListModel = useOrderListModel()
  const branchId = orderListModel.firstBranchId!

  return (
    <>
      <div className="branch-default-time-item">
        <Desktop>{icon}</Desktop>
        <TimeBadge
          variant={ETimeBadgeVariant.NORMAL}
          minutes={value}
          onClick={open}
        />
        {dialog}
      </div>
    </>
  )
}
