import { CircularProgress } from '@material-ui/core'
import Color from 'color'
import { cx } from 'emotion'
import { observer } from 'mobx-react-lite'
import React from 'react'
import useRefMounted from 'react-use/lib/useRefMounted'
import styled from 'styled-components'

import { OrderStateCategoryEnum } from '../../../graph/generated'
import { Colors } from '../../../styling'
import { OrderBox } from '../atoms/OrderBox'
import { useOrderListModel } from '../hooks/useOrderListModel'
import { TOrder, TOrderCategory, TOrdersCount } from '../order.types'

interface IProps {
  category: TOrderCategory
  categoryLabel: string
  orders: RoA<TOrder>
  ordersCount: TOrdersCount
  ordersLoading: boolean
  showBranches: boolean
}

const StyledOrderListCategory = styled.section`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-end;
  user-select: none;
  cursor: pointer;
  &:hover {
    background-color: ${Color(Colors.turquoise)
      .alpha(0.05)
      .string()};
  }
  &:active {
    background-color: ${Color(Colors.turquoise)
      .alpha(0.2)
      .string()};
  }

  & > h2 {
    color: ${Colors.greyLight};
    font-weight: lighter;
    font-size: 1.6rem;
    margin: 1.2rem 0 0.8rem 0.4rem;
  }
  & > .badge {
    margin: 1rem 0 1rem 1rem;
    min-height: 1.8rem;
    line-height: 1.8rem;
    min-width: 1.8rem;
    border-radius: 1.8rem;
    font-size: 1.2rem;
    text-align: center;
    vertical-align: middle;
    padding: 0;
    display: inline-block;
    background-color: ${Colors.black};
    color: ${Colors.white};
    &.bold {
      background-color: ${Colors.red};
      color: ${Colors.white};
    }
  }
  & > .arrow {
    width: 0;
    height: 0;
  }

  &.expanded .arrow {
    margin: 0 0.5rem 1.3rem 0.7rem;
    border-left: 0.5rem solid transparent;
    border-right: 0.5rem solid transparent;
    border-top: 0.5rem solid ${Colors.white};
  }

  &.collapsed {
    & .arrow {
      margin: 0 0.65rem 1.3rem 1rem;
      border-top: 0.5rem solid transparent;
      border-bottom: 0.5rem solid transparent;
      border-left: 0.5rem solid ${Colors.white};
    }
    & > h2 {
      color: ${Colors.greyMiddle};
    }
  }

  > .loader {
    color: ${Colors.greyLight};
    margin-left: 1rem;
    position: relative;
    top: -1.2rem;
  }
`

const alwaysExpandedCategories = [OrderStateCategoryEnum.NewOrders]

export const OrderListCategory = observer<IProps>(
  ({
    category,
    categoryLabel,
    orders,
    ordersCount,
    ordersLoading,
    showBranches,
  }) => {
    const orderListModel = useOrderListModel()

    const isCollapsed = orderListModel.isCategoryCollapsed(category)

    const onClick = React.useCallback(
      () => orderListModel.toggleCategoryCollapse(category),
      [category, orderListModel],
    )

    const renderOrder = (order: TOrder) => (
      <OrderBox
        key={order.id}
        order={order}
        showBranch={showBranches}
        isCancelled={order.orderStateCategory === OrderStateCategoryEnum.Cancel}
      />
    )

    const rootClassName = cx([
      { collapsed: isCollapsed, expanded: !isCollapsed },
    ])

    const isMounted = useRefMounted()

    const hasCount = typeof ordersCount === 'number'

    const badgeClassName = cx(
      'badge',
      { animated: isMounted.current === true && hasCount },
      { heartBeat: isMounted.current === true },
      { bold: category === OrderStateCategoryEnum.NewOrders },
    )

    const showLoader =
      !hasCount || (!isCollapsed && ordersCount !== orders.length)

    const canCollapse = !alwaysExpandedCategories.includes(category)

    // To fix state which might include fixed category as collapsed
    // Can be removed somewhere in the future
    React.useEffect(() => {
      if (!canCollapse && orderListModel.isCategoryCollapsed(category)) {
        orderListModel.toggleCategoryCollapse(category)
      }
    }, [orderListModel, canCollapse, category])

    return (
      <>
        <StyledOrderListCategory
          onClick={canCollapse ? onClick : undefined}
          className={rootClassName}
        >
          {canCollapse && <div className="arrow" />}
          <h2>{categoryLabel}</h2>
          <div className={badgeClassName} key={orders.length}>
            {hasCount ? ordersCount : '?'}
          </div>
          {showLoader && ordersLoading && (
            <CircularProgress className="loader" size={20} thickness={4} />
          )}
        </StyledOrderListCategory>
        {!isCollapsed && orders.map(renderOrder)}
      </>
    )
  },
)
