import {
  OrderStateCategoryEnum,
  QOrderListOrders,
  QOrderListQuery,
  SOrderListSubscription,
  useQOrderList,
  useSOrderList,
} from '../../../graph/generated'
import { ARRAY_EMPTY } from '../../../helpers/form'
import { useOnConnectionRestored } from '../../../hooks/useOnConnectionRestored'
import { DF } from '../../../tools/datefns'
import { RM } from '../../../tools/ramda'
import { TCategorizedOrders, TOrder, TOrderCategory } from '../order.types'
import { useOrderListQueryControl } from './useOrderListQueryControl'

export function useOrderListOrders(queryCategories: RoA<TOrderCategory>) {
  const { branchesFilter, skipQuery } = useOrderListQueryControl(
    queryCategories,
  )

  const { getStash, loading, refetch, updateQuery } = useQOrderList(
    { branches: branchesFilter, categories: queryCategories },
    { skip: skipQuery },
  )

  useOnConnectionRestored(refetch)

  useSOrderList(
    { branches: branchesFilter, categories: queryCategories },
    {
      skip: skipQuery,
      onSubscriptionData({ subscriptionData }) {
        if (subscriptionData.data) {
          updateQuery(prev => onOrderListUpdate(subscriptionData.data!, prev))
        }
      },
    },
  )

  const categorizeOrders = (orders: RoA<TOrder>) => {
    return orders.reduce(
      (result, order) => {
        const category = order.orderStateCategory
        if (!queryCategories.includes(category)) {
          return result
        }
        if (!result[category]) {
          result[category] = [order]
        } else {
          result[category].push(order)
        }
        return result
      },
      {} as TCategorizedOrders,
    )
  }

  const categorizedOrders = getStash(data => {
    return categorizeOrders(data.orders)
  })

  const getCategoryOrders = (category: TOrderCategory) => {
    if (categorizedOrders === null) {
      return ARRAY_EMPTY
    }
    return categorizedOrders[category] || ARRAY_EMPTY
  }

  return { getCategoryOrders, ordersLoading: loading }
}

function onOrderListUpdate(
  update: SOrderListSubscription,
  prev: QOrderListQuery,
) {
  const { order } = update.updatedOrder
  if (!order) {
    return prev
  }
  let orderExists = false
  const orders = RM.reject(isOldFinishedOrder, prev.orders).map(prevOrder => {
    if (prevOrder.id === order.id) {
      orderExists = true
      return order
    }
    return prevOrder
  })
  if (!orderExists) {
    orders.unshift(order)
  }
  return { ...prev, orders }
}

function isOldFinishedOrder(order: QOrderListOrders) {
  return (
    order.orderStateCategory === OrderStateCategoryEnum.Finished &&
    DF.differenceInHours(new Date(), order.createdAt) >
      order.companyBranch.oldOrderThreshold
  )
}
