import { Tooltip } from '@material-ui/core'
import { Alarm, DirectionsCar, Store } from '@material-ui/icons'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { mediaDesktop, mediaMobile } from '../../../app/Responsive'
import { MarginBox } from '../../../atoms/MarginBox'
import { ETimeBadgeVariant, TimeBadge } from '../../../atoms/TimeBadge'
import {
  DeliveryTypeEnum,
  useQBranchDefeaultTimes,
} from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { useOrderListModel } from '../hooks/useOrderListModel'
import { BranchDefaultTimeBadge } from '../molecules/BranchDefaultTimeBadge'

const StyledBranchDefaultTime = styled.div`
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  ${mediaDesktop`
    padding: 0.5rem 1rem;
  `};
  ${mediaMobile`
    padding: 1rem 0.5rem 0 0;
  `};
  .branch-default-time-item {
    margin: 0.4rem;
    display: flex;
  }
  .branch-default-time-item svg {
    margin: 0.2rem;
  }
  .branch-default-time-disabled {
    opacity: 0.3;
    cursor: no-drop;
  }
`

export const BranchDefaultTime = observer(() => {
  const orderListModel = useOrderListModel()

  const branchId = orderListModel.firstBranchId

  const { data, loading } = useQBranchDefeaultTimes(
    { branchId: branchId! },
    { skip: !branchId },
  )

  if (!branchId) {
    return (
      <StyledBranchDefaultTime>
        <Tooltip title={i18n.t`Select a single branch to set delivery time`}>
          <div className="branch-default-time-disabled">
            <TimeBadge variant={ETimeBadgeVariant.UNKNOWN}>
              <MarginBox margin="0.2rem 0 0 0">
                <Alarm />
              </MarginBox>
            </TimeBadge>
          </div>
        </Tooltip>
      </StyledBranchDefaultTime>
    )
  }

  if (loading || !data) {
    return null
  }

  const { companyBranch } = data

  return (
    <StyledBranchDefaultTime>
      <BranchDefaultTimeBadge
        deliveryType={DeliveryTypeEnum.Messenger}
        value={companyBranch.defaultDeliveryTime}
        title={i18n.t`Change default time of delivery to customer (in minutes)`}
        successText={i18n.t`Default time of delivery to customer is changed`}
        icon={<DirectionsCar />}
      />
      <BranchDefaultTimeBadge
        deliveryType={DeliveryTypeEnum.Pickup}
        value={companyBranch.defaultPickupTime}
        title={i18n.t`Change default time of pickup (in minutes)`}
        successText={i18n.t`Default time of pickup is changed`}
        icon={<Store />}
      />
    </StyledBranchDefaultTime>
  )
})
