import { disableBatching } from '@speedlo/graphql'
import React from 'react'

import {
  OrderStateCategoryEnum,
  useQOrderListCategories,
} from '../../../graph/generated'
import { RM } from '../../../tools/ramda'
import { useOrderListModel } from './useOrderListModel'

export const orderListCategories = [
  OrderStateCategoryEnum.NewOrders,
  OrderStateCategoryEnum.Preparing,
  OrderStateCategoryEnum.ReadyToDeliver,
  OrderStateCategoryEnum.OnWay,
  OrderStateCategoryEnum.Finished,
]

const findCategory = RM.propEq('enum')

export function useOrderListCategories() {
  const orderListModel = useOrderListModel()

  const { getStash } = useQOrderListCategories(
    {},
    { fetchPolicy: 'cache-first', ...disableBatching },
  )

  const categories = getStash(data => data.categories)

  const getCategoryLabel = React.useCallback(
    (category: OrderStateCategoryEnum) => {
      return categories ? categories.find(findCategory(category))!.label : '...'
    },
    [categories],
  )

  const queryCategories = orderListCategories.filter(
    category => !orderListModel.collapsedCategories.includes(category),
  )

  return {
    allCategories: orderListCategories,
    queryCategories,
    getCategoryLabel,
  }
}
