import { disableBatching } from '@speedlo/graphql'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useGoogleMap } from '../../../atoms/BaseMap'
import {
  DeviceStateEnum,
  QOrderMapDeliveriesQuery,
  SOrderMapDeliveriesDevice,
  useQOrderMapDeliveries,
  useSOrderMapDeliveries,
} from '../../../graph/generated'
import { gpsToLatLng } from '../../../helpers/gps'
import { useOnConnectionRestored } from '../../../hooks/useOnConnectionRestored'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { OrderMapDelivery } from '../atoms/OrderMapDelivery'
import { useOrderListQueryControl } from '../hooks/useOrderListQueryControl'
import { TMapDelivery } from '../order.types'

export const OrderMapDeliveries = observer(() => {
  const { branchesFilter: branches, noFilter } = useOrderListQueryControl()

  const { getStash, refetch, updateQuery } = useQOrderMapDeliveries(
    { branches },
    {
      skip: noFilter,
      ...disableBatching,
    },
  )

  useOnConnectionRestored(refetch)

  useSOrderMapDeliveries(
    { branches },
    {
      skip: noFilter,
      onSubscriptionData({ subscriptionData: { data: dataUpdate } }) {
        if (!(dataUpdate && dataUpdate.update && dataUpdate.update.device)) {
          return
        }
        updateQuery(prev =>
          onOrderMapDeliveriesUpdate(dataUpdate.update.device!, prev),
        )
      },
    },
  )

  const map = useGoogleMap()
  const { enqueueWarning } = useSnackBar()

  const deliveries = getStash(data => {
    return data.deliveries.filter(driver => driver.device !== null)
  })

  if (noFilter || deliveries === null) {
    return null
  }

  const renderDelivery = (delivery: TMapDelivery) => {
    const gps =
      delivery.device!.lastPosition !== null
        ? delivery.device!.lastPosition!.gps
        : null
    return (
      <OrderMapDelivery
        key={delivery.id}
        position={gps}
        label={delivery.initials || delivery.id}
        deviceState={normalizeDeviceState(delivery)}
        onClick={() => {
          if (!gps) {
            enqueueWarning(i18n.t`Driver position is unknown`)
          } else if (map) {
            map.panTo(gpsToLatLng(gps))
          }
        }}
      />
    )
  }

  return <>{deliveries.map(renderDelivery)}</>
})

export function onOrderMapDeliveriesUpdate(
  device: SOrderMapDeliveriesDevice,
  prev: QOrderMapDeliveriesQuery,
) {
  const deliveries = prev ? [...prev.deliveries] : []
  const idx = deliveries.findIndex(driver =>
    Boolean(driver.device && driver.device.id === device.id),
  )
  if (idx >= 0) {
    if (device.isFinalized) {
      deliveries.splice(idx, 1)
    } else {
      deliveries[idx] = {
        ...prev.deliveries[idx],
        countUndeliveredOrders: device.user.countUndeliveredOrders,
        initials: device.user.initials,
        device,
      }
    }
  } else {
    const {
      user: { __typename, id, initials, countUndeliveredOrders },
      ...userDevice
    } = device

    deliveries.push({
      __typename,
      id,
      initials,
      countUndeliveredOrders,
      device: userDevice,
    })
  }

  return {
    ...prev,
    deliveries,
  }
}

function normalizeDeviceState(delivery: TMapDelivery) {
  if (
    delivery.device &&
    delivery.device.transportationDevice.deviceState.enum ===
      DeviceStateEnum.Broken
  ) {
    return DeviceStateEnum.Broken
  }
  if (delivery.countUndeliveredOrders > 0) {
    return DeviceStateEnum.Busy
  }
  return DeviceStateEnum.Working
}
