import { useNow } from '@speedlo/hooks'
import { observable } from 'mobx'
import { observer } from 'mobx-react-lite'
import React from 'react'

import driverReturns from '../../../assets/driverReturns.mp3'
import { useSDriverReturns } from '../../../graph/generated'
import { formatFullName } from '../../../helpers/formats'
import { useAudioPlayer } from '../../../hooks/useAudioPlayer'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { useOrderListModel } from '../hooks/useOrderListModel'

interface IReturn {
  userId: ID
  zoneId: ID
  dateTime: Date
}

export const ReturningDrivers = observer<NoChildren>(() => {
  const now = useNow()
  const orderListModel = useOrderListModel()
  const { enqueueInfo } = useSnackBar()

  const [audio, controls, { muted, isPlaying }] = useAudioPlayer({
    src: driverReturns,
  })

  function notifyAboutReturn(zoneName: string, driverName: string) {
    if (!isPlaying && !muted) {
      controls.play()
    }
    enqueueInfo(
      i18n.t`Driver ${driverName} has reached return zone ${zoneName}`,
    )
  }

  const [returns] = React.useState(() => observable.map<ID, IReturn>())

  useSDriverReturns(
    { branches: orderListModel.branchesFilter },
    {
      skip: !orderListModel.hasBranchFilter,
      fetchPolicy: 'no-cache',
      onSubscriptionData: ({ subscriptionData: { data } }) => {
        if (!(data && data.update.device)) {
          return
        }

        const { user, returnZone, returning } = data.update.device
        if (!returnZone) {
          return
        }

        const key = `${user.id}-${returnZone.id}`

        if (returning && returnZone) {
          if (returns.has(key)) {
            return
          }

          returns.set(key, {
            userId: user.id,
            zoneId: returnZone.id,
            dateTime: now(),
          })

          notifyAboutReturn(returnZone.name, formatFullName(user))
        } else {
          returns.delete(key)
        }
      },
    },
  )

  return audio
})
