import { DirectionsCar } from '@material-ui/icons'
import { OverlayView } from '@react-google-maps/api'
import { cn } from '@speedlo/ui'
import { css } from 'emotion'
import React from 'react'
import styled from 'styled-components/macro'

import { MapControl } from '../../../atoms/MapControl'
import { DeviceStateEnum } from '../../../graph/generated'
import { gpsToLatLng } from '../../../helpers/gps'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'

interface IStyledProps {
  deviceState: DeviceStateEnum
}

interface IProps extends IStyledProps {
  position: Maybe<GeoPoint>
  label: string
  onClick(): void
}

const StyledOrderMapDelivery = styled.div`
  color: ${Colors.red};
  &.broken {
    color: ${Colors.orange};
  }
  &.working {
    color: ${Colors.green};
  }
  position: relative;
`

const StyledOrderMapDeliveryLabel = styled.div`
  font-size: 0.8rem;
  font-weight: bold;
  text-align: center;
  background-color: ${Colors.white};
`

const StyledOrderMapDeliveryButton = styled.div`
  font-size: 1.7rem;
  text-align: center;
  text-shadow: 1px 1px ${Colors.greyDark};
`

const iconSize = css`
  font-size: 32px;
`

const StyledDriverTouchButton = styled(TouchButton)`
  margin-bottom: ${Spacing.Small};
`

const StyledBrokenMark = styled.div`
  font-size: 22px;
  position: absolute;
  top: 19px;
  right: -7px;
`

const classesFromState = (deviceState: DeviceStateEnum) =>
  cn(
    { broken: deviceState === DeviceStateEnum.Broken },
    { working: deviceState === DeviceStateEnum.Working },
  )

export const OrderMapDelivery: React.FC<IProps> = ({
  position,
  label,
  deviceState,
  onClick,
}) => {
  const getPixelPositionOffset = React.useCallback(
    (width, height) => ({
      x: -(width / 2),
      y: -(height / 2),
    }),
    [],
  )
  const memoPosition = React.useMemo(
    () => position && gpsToLatLng(position),
    // https://github.com/facebook/react/issues/16096
    // eslint-disable-next-line react-hooks/exhaustive-deps
    position ? [position.latitude, position.longitude] : [position],
  )
  return (
    <>
      <MapControl position="LEFT_TOP">
        <StyledDriverTouchButton onClick={onClick}>
          <StyledOrderMapDelivery className={classesFromState(deviceState)}>
            <StyledOrderMapDeliveryButton>{label}</StyledOrderMapDeliveryButton>
          </StyledOrderMapDelivery>
        </StyledDriverTouchButton>
      </MapControl>
      {memoPosition !== null && (
        <OverlayView
          position={memoPosition}
          mapPaneName={OverlayView.OVERLAY_MOUSE_TARGET}
          getPixelPositionOffset={getPixelPositionOffset}
        >
          <StyledOrderMapDelivery className={classesFromState(deviceState)}>
            <StyledOrderMapDeliveryLabel>{label}</StyledOrderMapDeliveryLabel>
            <DirectionsCar
              className={cn(iconSize, 'animated', 'pulse')}
              key={`${memoPosition.lat()}/${memoPosition.lng()}`}
            />
            {deviceState === DeviceStateEnum.Broken && (
              <StyledBrokenMark>!</StyledBrokenMark>
            )}
          </StyledOrderMapDelivery>
        </OverlayView>
      )}
    </>
  )
}
