import { Trans } from '@lingui/macro'
import { useReaction } from '@speedlo/hooks'
import React from 'react'
import { Field, IFieldApi } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { SelectControlFlat } from '../../../atoms/SelectControlFlat'
import {
  FPaymentMethodFragment as FPaymentMethod,
  useQBranchPaymentMethods,
} from '../../../graph/generated'
import { ARRAY_EMPTY } from '../../../helpers/form'
import { idPropEq, RM } from '../../../tools/ramda'
import { IOrderFormValues } from '../models/CreateOrderModel'

interface IProps {
  field: keyof IOrderFormValues
}

export const OrderFormPaymentMethod: React.FC<IProps> = ({ field }) => {
  const { createOrder } = useRoot()
  const fieldApiRef = React.useRef<IFieldApi | null>(null)

  // observe currentBranch manually because using observer would not update the rendered Field
  const [branchId, setBranchId] = React.useState(createOrder.currentBranch.id)
  useReaction(() => createOrder.currentBranch.id, id => setBranchId(id))
  const { getStash, error } = useQBranchPaymentMethods({
    branchId,
  })

  const paymentMethods = getStash(data => data.companyBranch.paymentMethods)

  const hasSomeMethod = paymentMethods && paymentMethods.length > 0

  const selectPaymentMethod = React.useCallback(
    (method: FPaymentMethod | null) => {
      if (fieldApiRef.current) {
        fieldApiRef.current.setValue(method ? method.id : null)
        createOrder.setPaymentValues(
          method ? method.paymentType.enum : null,
          method ? method.paymentGate.enum : null,
        )
      }
    },
    [createOrder],
  )

  // initial selection (on page load)
  React.useEffect(() => {
    // there was some strange race condition/memory leak/? when the setValue was called without any timeout
    // that resulted in the infinite loop of formValues updates
    setTimeout(() => {
      if (fieldApiRef.current) {
        fieldApiRef.current.setTouched(true)
        const noMethodSelected = !createOrder.formValues.paymentMethod

        if (noMethodSelected && hasSomeMethod && paymentMethods) {
          const firstMethod = RM.head(paymentMethods)
          selectPaymentMethod(firstMethod)
        }
      }
    }, 200)
  }, [createOrder, hasSomeMethod, paymentMethods, selectPaymentMethod])

  // when new paymentMethods doesn't contain previously selected one (on branch change)
  React.useEffect(() => {
    if (fieldApiRef.current && paymentMethods) {
      const selectedMethod = createOrder.formValues.paymentMethod
      const newMethodsContainSelected = paymentMethods.find(
        idPropEq(selectedMethod),
      )

      if (selectedMethod && !newMethodsContainSelected) {
        const firstMethod = hasSomeMethod ? RM.head(paymentMethods) : null
        selectPaymentMethod(firstMethod)
      }
    }
  }, [createOrder, hasSomeMethod, paymentMethods, selectPaymentMethod])

  if (paymentMethods === null) {
    return null
  }

  if (error || !hasSomeMethod) {
    return <Trans>Failed to retrieve available payment methods</Trans>
  }

  // pure={false} turns off optimization that would cause Field to not re-render when some outer var (eg. paymentMethods) updates
  return (
    <Field field={field} pure={false}>
      {(fieldApi: IFieldApi<ID>) => {
        fieldApiRef.current = fieldApi
        return (
          <SelectControlFlat<FPaymentMethod>
            items={paymentMethods || ARRAY_EMPTY}
            selectedItem={
              paymentMethods && paymentMethods.find(idPropEq(fieldApi.value))
            }
            formatItem={method => method.description || method.paymentGate.code}
            onSingleSelection={selectPaymentMethod}
          />
        )
      }}
    </Field>
  )
}
