import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { SelectControl } from '../../../atoms/SelectControl'
import { useQOrderFormPromo } from '../../../graph/generated'
import { ARRAY_EMPTY } from '../../../helpers/form'
import { i18n } from '../../../i18n'
import { RM } from '../../../tools/ramda'
import { TPromoCode } from '../createorder.types'
import { usePromoCodes } from '../hooks/usePromoCodes'

export const OrderFormPromo = observer(() => {
  const { createOrder } = useRoot()

  const { data, loading, error } = useQOrderFormPromo({
    branchId: createOrder.currentBranch.id,
  })

  const selectedCodes = usePromoCodes()

  if (error) {
    return <Trans>Failed retrieving available promo codes</Trans>
  }

  return (
    <SelectControl<TPromoCode>
      options={(data && data.promoCodes) || ARRAY_EMPTY}
      isLoading={loading}
      isMulti={true}
      isClearable={true}
      getOptionValue={RM.prop('id')}
      getOptionLabel={RM.prop('code')}
      placeholder={i18n.t`Promo codes`.toLocaleLowerCase()}
      noOptionsMessage={() => i18n.t`No promo codes available`}
      value={Array.from(selectedCodes.values())}
      onChange={value => {
        if (Array.isArray(value)) {
          selectedCodes.replace(value.map(code => [code.id, code]))
        } else {
          selectedCodes.clear()
        }
      }}
    />
  )
})
