import { Assignment } from '@material-ui/icons'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

import { SizerButton } from '../../../atoms/SizerButton'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'
import { ROUTE_OVERVIEW_STATS_ORDERS } from '../../overview/overview.routes'
import { useOrderListModel } from '../hooks/useOrderListModel'

interface IProps {}

const DivTopRightStyled = styled(SizerButton)`
  display: inline-block;
`

export const StatsFullscreenButton: React.FC<IProps> = () => {
  const orderListModel = useOrderListModel()
  return useObserver(() => (
    <DivTopRightStyled>
      <Link to={`${ROUTE_OVERVIEW_STATS_ORDERS}/today`}>
        <TouchButton
          icon={Assignment}
          color={Colors.greyMiddle}
          disabled={!orderListModel.isSingleBranchSelected}
        />
      </Link>
    </DivTopRightStyled>
  ))
}
