import { DateTimePickerProps } from '@material-ui/pickers'
import React from 'react'
import { Field, FieldType } from 'react-form'

import { InputDateTime } from './InputDateTime'

interface IProps
  extends NoChildren,
    Omit<DateTimePickerProps, 'value' | 'onChange' | 'children'> {
  field: FieldType
}

export const FormInputDateTime: React.FC<IProps> = ({
  field,
  ...pickerProps
}) => {
  return (
    <Field
      field={field}
      render={fieldApi =>
        React.createElement(InputDateTime, {
          value: fieldApi.value || new Date(),
          onChange: value => fieldApi.setValue(value),
          onOpen: () => fieldApi.setTouched(true),
          ...pickerProps,
        })
      }
    />
  )
}
