import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { BlockLoader } from '../../../atoms/BlockLoader'
import { Card } from '../../../atoms/Card'
import { AutoHeight } from '../../../atoms/FlexAutoHeight'
import { useRoot } from '../../../atoms/RootProvider'
import { useQOrderMenu } from '../../../graph/generated'
import { RM } from '../../../tools/ramda'
import { useOrderCreateControl } from '../hooks/useOrderCreateControl'
import { useOrderMenuFilter } from '../hooks/useOrderMenuFilter'
import { OrderMenuCategories } from '../molecules/OrderMenuCategories'
import { OrderMenuFilter } from '../molecules/OrderMenuFilter'
import { OrderMenuRecipes } from '../molecules/OrderMenuRecipes'
import { OrderSidedishModal } from '../molecules/OrderSidedishModal'

const FilterCard = styled(Card)`
  flex: 0 0 auto;
`

const CategoriesCont = styled.div`
  flex: 0 0 auto;
`

export const OrderMenu: React.FC = observer(() => {
  const { createOrder } = useRoot()

  const { data, error, loading } = useQOrderMenu({
    branchId: createOrder.currentBranch.id,
  })

  const orderMenu = data && data.orderMenu

  const { onRecipeAdd } = useOrderCreateControl()

  const {
    observableFilter,
    computedRecipes,
    toggleCategory,
    searchByText,
  } = useOrderMenuFilter(orderMenu)

  const onConfirm = React.useCallback(() => {
    const recipes = computedRecipes.get()
    if (recipes.length === 1) {
      onRecipeAdd(recipes[0])
      return true
    }
    return false
  }, [computedRecipes, onRecipeAdd])

  if (loading) {
    return <BlockLoader />
  }

  if (error || !orderMenu) {
    return null
  }

  return (
    <AutoHeight>
      <FilterCard>
        <OrderMenuFilter
          recipeCount={computedRecipes.get().length}
          selectedCategory={
            orderMenu.find(RM.propEq('id', observableFilter.categoryId)) || null
          }
          onSearch={searchByText}
          onConfirm={onConfirm}
        />
      </FilterCard>
      <CategoriesCont>
        <OrderMenuCategories
          categories={orderMenu}
          activeCategory={observableFilter.categoryId}
          onCategoryClick={toggleCategory}
        />
      </CategoriesCont>
      <OrderMenuRecipes
        recipes={computedRecipes.get()}
        onRecipeUse={onRecipeAdd}
      />
      <OrderSidedishModal />
    </AutoHeight>
  )
})
