import React from 'react'
import { Link } from 'react-router-dom'

import { Sizer } from '../../../atoms/Sizer'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'
import { useOrderCallStatus } from '../hooks/useOrderCallStatus'

interface IProps {}

export const CreateOrderButton: React.FC<IProps> = props => {
  const { location, badge } = useOrderCallStatus()
  return (
    <Link to={location}>
      <Sizer height={Spacing.ButtonHeight}>
        <TouchButton
          text={i18n.t`Create order`}
          color={Colors.turquoise}
          badge={badge}
        />
      </Sizer>
    </Link>
  )
}
