import React from 'react'
import styled, { css } from 'styled-components/macro'

import { Card } from '../../../atoms/Card'
import { getPictogramClassName } from '../../../atoms/PictogramDisplay'
import { ColorBadge } from './ColorBadge'
import { FOrderMenuCategoryFragment } from '../../../graph/generated'
import { applyAlphaToColor, Colors } from '../../../styling'

interface IProps {
  category: FOrderMenuCategoryFragment
  isActive: boolean
  onClick(): void
}

const applyAlpha = (alphaValue: number) => () =>
  applyAlphaToColor(Colors.yellow, alphaValue)

const StyledCategoryCard = styled(Card)`
  flex: 1;
  cursor: pointer;
  padding: 0.5rem;
  margin: 0 0.3rem 0.5rem 0;
  min-width: 5.3rem;
  max-width: 5.3rem;
  min-height: 3.9rem;
  &:hover {
    background-color: ${applyAlpha(0.85)};
    color: ${Colors.black};
  }
  ${({ isActive }: { isActive: boolean }) =>
    isActive
      ? css`
          background-color: ${Colors.green};
          color: ${Colors.white};
        `
      : ''};

  .category-pictogram {
    span:before,
    span::after {
      margin: 0;
      padding: 0;
      font-size: 2.5rem;
    }
  }

  .category-name {
    position: absolute;
    font-size: 0.7rem;
    text-transform: uppercase;
    margin-left: 0.3rem;
    width: 2rem;
  }
`

export const OrderMenuCategory: React.FC<IProps> = ({
  category,
  isActive,
  onClick,
}) => {
  return (
    <StyledCategoryCard isActive={isActive} onClick={onClick}>
      <ColorBadge color={category.color?.color} />
      <span className="category-pictogram">
        <span className={getPictogramClassName(category.pictogram || '')} />
      </span>
      <span className="category-name">{getShortName(category.nameLabel!)}</span>
    </StyledCategoryCard>
  )
}

function getShortName(fullname: string) {
  return fullname
    .split(/\s+/)
    .map(word => word.substr(0, 3))
    .filter(word => word.length >= 3)
    .join(' ')
}
