import React from 'react'

import { FCreateOrderItems, useMAddToSector } from '../../../graph/generated'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { TouchButton } from '../../../molecules/TouchButton'

type TProps = NoChildren & {
  items: RoA<FCreateOrderItems>
}

export const OrderCartDevSector: React.FC<TProps> = ({ items }) => {
  const [addToSector] = useMAddToSector()
  const { enqueueSuccess } = useSnackBar()

  if (process.env.REACT_APP_STAGE === 'prod') {
    return null
  }

  return (
    <TouchButton
      onClick={async () => {
        const sectorId = window.prompt('Zadej ID sektoru')
        if (!sectorId) {
          return
        }
        await Promise.all(
          items.map(async item => {
            await addToSector({
              sectorId: sectorId,
              recipeId: item.recipe.id,
              sideDishes: item.sidedishes.map(s => s.recipe.id),
            })
          }),
        )
        enqueueSuccess('Je to tam ... snad')
      }}
    >
      Přidat do sektoru [DEV]
    </TouchButton>
  )
}
