import React from 'react'
import { Route, Switch } from 'react-router-dom'

import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import { CreateOrderPage } from '../../createorder/pages/CreateOrderPage'
import {
  ROUTE_ORDER_CREATE,
  ROUTE_ORDER_MAP,
  ROUTE_ORDER_OVERVIEW,
} from '../order.routes'
import { OrderMapPage } from '../pages/OrderMapPage'
import { OrderOverviewPage } from '../pages/OrderOverviewPage'

interface IProps {}

export const OrderRouting: React.FC<IProps> = () => (
  <Switch>
    <SecureRoute
      exact
      path={ROUTE_ORDER_OVERVIEW}
      component={OrderOverviewPage}
    />
    <SecureRoute path={ROUTE_ORDER_CREATE} component={CreateOrderPage} />
    <SecureRoute path={ROUTE_ORDER_MAP} component={OrderMapPage} />
    <Route component={NotFoundPage} />
  </Switch>
)

export default OrderRouting
