import { Tooltip } from '@material-ui/core'
import { Stop } from '@material-ui/icons'
import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

import { SizerButton } from '../../../atoms/SizerButton'
import { useQueryParams } from '../../../hooks/useQueryParams'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'
import { ROUTE_SETTINGS_HOURS } from '../../settings/settings.routes'

interface IProps {
  branchId: MaybeID
  isBranchClosed: boolean
  onClick(): void
}

const StyledFastCloseButton = styled.div`
  display: inline-block;
  margin-right: ${Spacing.Middle};
`

export const FastCloseButton: React.FC<IProps> = ({
  branchId,
  isBranchClosed,
  onClick,
}) => {
  const { createQueryString } = useQueryParams()

  let button

  if (branchId === null) {
    button = (
      <Tooltip title={i18n.t`Select a single branch for closing`}>
        <TouchButton icon={Stop} color={Colors.greyMiddle} disabled={true} />
      </Tooltip>
    )
  } else if (isBranchClosed) {
    button = (
      <Link to={`${ROUTE_SETTINGS_HOURS}?${createQueryString({ branchId })}`}>
        <TouchButton icon={Stop} color={Colors.red} />
      </Link>
    )
  } else {
    button = (
      <TouchButton icon={Stop} color={Colors.greyMiddle} onClick={onClick} />
    )
  }

  return (
    <StyledFastCloseButton>
      <SizerButton>{button}</SizerButton>
    </StyledFastCloseButton>
  )
}
