import { useReaction } from '@speedlo/hooks'
import { debounce } from 'debounce'

import { useRoot } from '../../../atoms/RootProvider'
import {
  MCreateOrderUpdateVariables,
  useMCreateOrderUpdate,
} from '../../../graph/generated'

export function useUpdateOrder() {
  const { createOrder } = useRoot()
  const [updateOrder] = useMCreateOrderUpdate()

  const debouncedUpdate = debounce((variables: MCreateOrderUpdateVariables) => {
    if (createOrder.orderId) {
      updateOrder(variables)
    }
  }, 2000)

  // update order whenever these would change
  useReaction(() => createOrder.updateOrderVariables, debouncedUpdate)

  return updateOrder
}
