import { Observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { CanMutate } from '../../../atoms/CanMutate'
import { CanQuery } from '../../../atoms/CanQuery'
import { PageLayoutGridItem } from '../../../atoms/PageLayoutGridItem'
import { useRoot } from '../../../atoms/RootProvider'
import { i18n } from '../../../i18n'
import { PageLayoutFullHeightItem } from '../../../molecules/PageLayoutFullHeightItem'
import { PageLayoutItem } from '../../../molecules/PageLayoutItem'
import { PageLayoutResponsive } from '../../../molecules/PageLayoutResponsive'
import { CreateOrderButton } from '../molecules/CreateOrderButton'
import { BranchDefaultTime } from '../organisms/BranchDefaultTime'
import { FastClose } from '../organisms/FastClose'
import { MapFullscreenButton } from '../organisms/MapFullscreenButton'
import { OrderList } from '../organisms/OrderList'
import { OrderListBranchFilter } from '../organisms/OrderListBranchFilter'
import { OrderMap } from '../organisms/OrderMap'
import { ReturningDrivers } from '../organisms/ReturningDrivers'
import { StatsFullscreenButton } from '../organisms/StatsFullscreenButton'

const StyledOrderOverviewPage = styled(PageLayoutResponsive)`
  & .filter-container {
    margin: -0.05rem 0 0 0;
  }
  & .top-bar-container {
    display: flex;
  }
`

export const OrderOverviewPage: React.FC = () => {
  const { user } = useRoot()
  return (
    <StyledOrderOverviewPage
      pageTitle={i18n.t`Orders`}
      showSoundControl={true}
      topContent={
        <div className="top-bar-container">
          <CanMutate mutationName="updateDefaultOrderProcessTime">
            <BranchDefaultTime />
          </CanMutate>
          <CanQuery queryName="businessHours">
            <CanMutate mutationName="addSpecialClosingBusinessHours">
              <FastClose />
            </CanMutate>
          </CanQuery>
          <CanQuery queryName="statistics">
            <StatsFullscreenButton />
          </CanQuery>
        </div>
      }
    >
      <PageLayoutGridItem gridColumn={1} gridColumnSpan={6} gridRow={1}>
        <div className="filter-container">
          <OrderListBranchFilter />
        </div>
      </PageLayoutGridItem>

      <ReturningDrivers />

      <Observer>
        {() => (
          <PageLayoutFullHeightItem
            withScrollArea={true}
            row={user.hasSingleBranch ? 1 : 2}
            rowInMobile={4}
            widthInColumns={8}
            scrollAreaId="order-list"
          >
            <OrderList />
          </PageLayoutFullHeightItem>
        )}
      </Observer>

      <PageLayoutFullHeightItem
        widthInColumns={8}
        column={8}
        rowInMobile={5}
        withScrollArea={false}
        forcePercentHeightInMobile={90}
      >
        <OrderMap />
        <MapFullscreenButton />
      </PageLayoutFullHeightItem>

      <PageLayoutItem
        gridColumn={7}
        gridColumnSpan={2}
        gridRow={1}
        gridRowInMobile={2}
        canMutate="createOrder"
      >
        <CreateOrderButton />
      </PageLayoutItem>
    </StyledOrderOverviewPage>
  )
}
